import Vue from 'vue';
import VueRouter from "vue-router";
import Page404 from "./404.vue";

import Login from "./containers/login.vue";
import Home from "./containers/home.vue";
import Dashboard from "./containers/dashboard.vue";
import colors from "./containers/colors.vue";
import mapData from "./containers/mapData.vue";
import mapAttrs from "./containers/mapAttrs.vue";

Vue.use(VueRouter);


const routes = [{
        path: '/',
        component: Home,
        redirect: '/dashboard',
        children: [{
            path: "dashboard",
            component: Dashboard,
        },{
            path: "mapData",
            component: mapData
        },{
            path: "mapAttrs",
            component: mapAttrs
        },{
            path: "colors",
            component: colors
        }],
    },
    {
        path: '/login',
        component: Login
    },
    {
        path: '*',
        component: Page404,
    }
]


const router = new VueRouter({
    routes // (缩写) 相当于 routes: routes
});

export default router;