<template>
    <div>
        <el-card class="common_box">
            <h3 class="title">地图颜色管理</h3>
            <br/>
            <el-button type="primary" @click="addFunc">添加分类</el-button> 
            <br/>
            <br/>
            <el-table
                border
                stripe
                :data="tableData"
                style="width: 100%">
                <el-table-column prop="typeName" label="分类Key" width="100"></el-table-column>
                <el-table-column prop="typeData" label="分类属性" width="180"></el-table-column>
                <el-table-column label="功能">
                    <template slot-scope="scope">  
                        <el-button type="danger" @click="delFunc(scope.row)" size="mini">删除</el-button>   
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <el-dialog
            title="添加颜色分类"
            :visible.sync="visible"
            width="50%"
            center>
            <el-form :model="form" label-position="top" label-width="100px">
                <el-form-item label="属性key">
                    <el-input v-model="form.typeName"></el-input>
                </el-form-item>
                <el-form-item label="属性值">
                    <el-input 
                        type="textarea" 
                        rows="7" 
                        v-model="form.typeData">
                    </el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="reset">取 消</el-button>
                <el-button type="primary" @click="doAddFunc">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import ajax, { cookie } from "@/util.js";
export default {
    data() {
        return {
            user: "",
            visible: false,
            tableData:[],
            form: {
                typeName: "",
                typeData: ""
            }
        };
    },
    inject: ["reload"],
    created() {
        this.user = cookie.get('user');
    },
    mounted() {
        this.getData();
    },
    methods: {
        getData() {
            ajax.get("/admin/type/list", {}).then(res => {
                if (res.code == 200) {
                    this.tableData = res.data;
                }
            }).catch(err=> {
                console.log(err);
            });
        },
        reset() {
            this.visible = false;
            this.form = {
                typeName: "",
                typeData: ""
            };
        },
        addFunc() {
            this.visible = true;
        },
        doAddFunc() {
            if (!this.form.typeName) {
                this.$message.error("请填写属性key");
            } else if (!this.form.typeData) {
                this.$message.error("请填写属性值");
            } else {
                ajax.post("/admin/type/add", {
                    ...this.form
                }).then(res => {
                    console.log(res);
                }); 
            }
        },
        delFunc(row) {
            this.$confirm('此操作将永久删除该项, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                ajax.post("/admin/type/del?typeName=" + row.typeName, {}).then((res) => {
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                    this.reload();
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        }
    }
}
</script>
<style scoped>
    .common_box{
        min-height: 300px;
    }
    .common_box .title {
        margin-bottom: 0;
    }
</style>
