<template>
    <div>
        <el-card class="common_box">
            <h3 class="title">地图数据上传</h3>
            <br/>
            <el-table
                border
                size="small"
                stripe
                :data="tableData"
                style="width: 100%">
                <el-table-column prop="floor" label="楼层" width="100"></el-table-column>
                <el-table-column prop="fileName" label="名称" width="180"></el-table-column>
                <el-table-column prop="order" label="排序" width="180"></el-table-column>
                <el-table-column label="功能">
                    <template slot-scope="scope">
                        <el-button type="primary" @click="showFixBox(scope.row)" size="mini">修改</el-button>   
                        <el-button type="danger" @click="deleteFunc(scope.row)" size="mini">删除</el-button>   
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <el-dialog
            title="修改地图数据上传"
            :visible.sync="visible"
            width="40%"
            center>
            <el-form :model="form" label-width="100px">
                <el-form-item label="楼层" prop="floor">
                    <el-input v-model="form.floor"></el-input>
                </el-form-item>
                <el-form-item label="名称" prop="fileName">
                    <el-input v-model="form.fileName"></el-input>
                </el-form-item>
                <el-form-item label="文件" prop="geofile">
                    <input type="file" @change="fileChange" class="file"></input>
                </el-form-item>
                <el-form-item label="排序" prop="order">
                    <el-input-number v-model="form.order"></el-input-number>
                </el-form-item>
                <el-form-item label="类型" prop="geometry">
                    <el-select v-model="form.geometry" placeholder="请选择类型">
                        <el-option label="Point" value="Point"></el-option>
                        <el-option label="LineString" value="LineString"></el-option>
                        <el-option label="Polygon" value="Polygon"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="reset">取 消</el-button>
                <el-button type="primary" @click="fixFunc">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import ajax, { cookie } from "@/util.js";
export default {
    data() {
        return {
            user: "",
            form: {},
            visible: false,
            tableData:[] ,
            file: null
        };
    },
    inject: ['reload'],
    created() {
        this.user = cookie.get('user');
    },
    mounted() {
        this.getData();
    },
    methods: {
        getData() {
            ajax.post("/admin/floor/list", {}).then(res => {
                if (res.code == 200) {
                    this.tableData = res.data;
                }
            }).catch(err=> {
                console.log(err);
            });
        },
        fileChange(event) {
            var file = event.target.files[0];
            this.file = file;
        },
        showFixBox(row) {
            this.visible = true;
            console.log(row)
            this.form = row;
        },
        fixFunc() {
            if(!this.file){
                this.$message({
                    type: 'warning',
                    message: '请选择要上传的文件'
                });
            } else {
                ajax.postFormData(
                    `/admin/floor/upload/${this.form.geometry}/${this.form.floor}/${this.form.fileName}/${this.form.order}`, {
                    geofile: this.file
                }).then((res) => {
                    if (res.code == 200) {
                        this.reset();
                        this.reload();
                    }
                });
            }
        },
        reset() {
            this.visible = false;
            this.form = {};
        },
        deleteFunc(row) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                ajax.post(`/admin/floor/del/${row.floor}/${row.fileName}`, {}).then((res) => {
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                    this.reload();
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        }
    }
}
</script>
<style  scoped>
    .common_box{
        min-height: 300px;
    }
    .common_box .title {
        margin-bottom: 0;
    }
</style>
