<template>
    <el-container>
        <el-header class="header">
            <div class="header_wrap">
                <h3 class="title">
                    <strong><svg-icon icon-class="map" /> 室内地图管理后台</strong>
                    <span>Indoor Map Management System</span>
                </h3>
                <div class="login_info">
                    <i class="el-icon-user-solid"></i>
                    <b>&nbsp;&nbsp;{{ user }}&nbsp;&nbsp;&nbsp;</b>
                    <el-button @click="logout" type="primary">退出登录</el-button>
                </div>
            </div>
        </el-header>
        <div class="nav">
            <el-menu :default-active="$route.path" mode="horizontal" :router="true">
                <el-menu-item index="/dashboard"><i class="el-icon-s-home" />仪表盘</el-menu-item>
                <el-submenu index="2">
                    <template slot="title"><i class="el-icon-menu" />我的工作台</template>
                    <el-menu-item index="/mapData">地图数据上传</el-menu-item>
                    <el-menu-item index="/mapAttrs">地图属性管理</el-menu-item>
                    <el-menu-item index="/colors">颜色管理</el-menu-item>
                </el-submenu>
            </el-menu>
        </div>
        <el-main class="main">
            <el-row :gutter="20">
                <el-col :span="24">
                    <router-view v-if="isRouterAlive"></router-view>
                </el-col>
            </el-row>
        </el-main>
    </el-container>
</template>

<script>
import { cookie } from "@/util.js";

export default {
    data() {
        return {
            dialogVisible: false,
            user: "",
            isRouterAlive: true
        };
    },
    provide (){
        return {
            reload: this.reload
        }
    },
    created() {
        this.user = cookie.get('user');
    },
    components: {},
    methods: {
        reload() {
            this.isRouterAlive = false;
            this.$nextTick(function() {
                this.isRouterAlive = true;
            });
        },
        logout() {
            cookie.del('token');
            cookie.del('user');
            this.$router.push({
                path: "/login"
            });
        }
    }
};
</script>

