<template>
    <div>
        <el-card class="common_box">
            <h3 class="title">地图属性管理</h3>
            <br/>
            <el-table
                border
                stripe
                :data="tableData"
                style="width: 100%">
                <el-table-column prop="uuid" label="ID" width="300"></el-table-column>
                <el-table-column prop="fileName" label="名字" width="80"></el-table-column>
                <el-table-column prop="floor" label="楼层" width="80"></el-table-column>
                <el-table-column label="属性">
                    <template slot-scope="scope">
                        <el-tag type="info">class => {{ scope.row.properties.class }}</el-tag>
                        <el-tag type="info">elev => {{ scope.row.properties.elev }}</el-tag>
                        <el-tag type="info">name => {{ scope.row.properties.name }}</el-tag>
                        <el-tag type="info">num => {{ scope.row.properties.num }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作"  width="100">
                    <template slot-scope="scope">
                        <el-button type="primary" @click="showFixBox(scope.row)" size="small">修改</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <br/>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[10, 20, 30, 50]"
                :page-size="10"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>

        <el-dialog
            title="修改地图属性"
            :visible.sync="visible"
            width="50%"
            center>
            <el-form label-position="top" label-width="100px">
                <el-form-item label="属性">
                    <el-input 
                        type="textarea" 
                        rows="10"                         
                        v-model="properties"> 
                    </el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="reset">取 消</el-button>
                <el-button type="primary" @click="fixFunc">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import ajax, { cookie } from "@/util.js";
export default {
    data() {
        return {
            user: "",
            currentPage: 1,
            tableData:[],
            visible: false,
            limit: 10,
            total: 0,
            properties: "",
            uuid: ""
        };
    },
    inject: ["reload"],
    created() {
        this.user = cookie.get('user');
    },
    mounted() {
        this.getData();
    },
    methods: {
        reset() {
            this.visible = false;
            this.properties = "";
            this.uuid = "";
        },
        showFixBox(row) {
            this.visible = true;
            this.properties = JSON.stringify(row.properties);
            this.uuid = row.uuid;
        },
        fixFunc() {
            if(!this.properties){
                this.$message({
                    type: 'warning',
                    message: '请填写属性信息'
                });
            } else {
                ajax.post("/admin/properties/update", {
                    uuid: this.uuid,
                    properties: JSON.parse(this.properties)
                }).then((res) => {
                    if (res.code == 200) {
                        this.reset();
                        this.reload();
                    }
                });
            }
        },
        handleSizeChange(val) {
            this.limit = val;
            this.getData();
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getData();
        },
        getData() {
            let offset = this.limit * (this.currentPage - 1);
            let limit = this.limit;
            ajax.get(`/admin/properties/list/${offset}/${limit}`, {}).then(res => {
                if (res.code == 200) {
                    this.tableData = res.data.properties;
                    this.total = res.data.count;
                }
            }).catch(err=> {
                console.log(err);
            });
        },
    }
}
</script>
<style  scoped>
    .common_box{
        min-height: 300px;
    }
    .common_box .el-tag{
        margin-right: 5px;
    }
    .common_box .el-tag:last-child{
        margin-right: 0;
    }
    .common_box .title {
        margin-bottom: 0;
    }
</style>
