<template>
    <el-card class="box-card common_box">
        <h3 class="title">欢迎信息</h3>
        <br />
        <el-alert type="success">
            <h3>欢迎<strong>{{user}}</strong>登录</h3>
        </el-alert>
        <br/>
        <div>
            <el-button @click="jump(1)"><svg-icon icon-class="upload" />&nbsp;地图数据上传</el-button>
            <el-button @click="jump(2)"><svg-icon icon-class="songshen" />&nbsp;地图属性管理</el-button>
            <el-button @click="jump(3)"><svg-icon icon-class="fenfa" />&nbsp;颜色管理</el-button>
        </div>
    </el-card>
</template>

<script>
import { cookie } from "@/util.js";
export default {
    data() {
        return {
            user: ""
        };
    },
    inject: ['reload'],
    created() {
        this.user = cookie.get('user');
    },
    methods:{
        jump(index) {
            if (index == 1) {
                this.$router.replace({path:"/mapData"});
            } else if (index == 2) {
                this.$router.push({path:"/mapAttrs"});
            } else if (index == 3) {
                this.$router.push({path:"/colors"});
            }
        }   
    }
}
</script>
<style  scoped>
    .common_box{
        min-height: 300px;
    }
    .common_box .title {
        margin-bottom: 0;
    }
</style>
